.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@keyframes emergence {
  0% {
    transform: translateY(800px); }
  100% {
    transform: translateY(0); } }

@keyframes motion {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(40px); }
  100% {
    transform: translateY(0); } }

@keyframes grow {
  0% {
    transform: scale(0); }
  50% {
    transform: scale(1); }
  75% {
    transform: scale(1); }
  100% {
    transform: scale(0.6); } }

